.buttons {
  justify-content: center;
  align-items: center;
  gap: 25px;
  padding-top: 120px;
  display: flex;
}

.btn-start {
  width: 200px;
  height: 100px;
  color: #3b270e;
  border: none;
  border-radius: 10px;
  font-size: 55px;
  transition-property: background-color, box-shadow;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.btn-start:hover, .btn-start:focus {
  background-color: #f5eddc;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.btn-stop {
  width: 200px;
  height: 100px;
  color: #3b270e;
  border: none;
  border-radius: 10px;
  font-size: 55px;
  transition-property: background-color, box-shadow;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.btn-stop:hover, .btn-stop:focus {
  background-color: #f5eddc;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

